import Vue from 'vue'
import axios from 'axios'
import {
	JSEncrypt
} from 'jsencrypt'

const service = axios.create({
	baseURL: process.env.VUE_APP_API, // 请求的接口
	timeout: 100000 // 请求超时时间
})

// 使用拦截器，定义全局请求头
service.interceptors.request.use(config => {
	// 私钥 和后端沟通写死了
	var publicKey = 'MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQCNyDK//LJJVVicgh9rbtwWscxx0ZM87clsVlc/UaoO8ZQ54H6bfwtRwwEzQrlKI02qmNIX91ntwKEPKbQ19VN2LINs+Y3QEHx9v6sEeRc/U7p5TuzZHoi09VeWdFfNRkVeDMWaT0QstywT/mv5qV0iLAULoFZCgh5F3it2anh9IQIDAQAB'
	// 新建JSEncrypt对象
	const encryptor = new JSEncrypt()
	// 设置公钥
	encryptor.setPublicKey(publicKey)
	let Authorization= encryptor.encrypt('qxiaohuq')
	// 在请求头中添加token
	config.headers.Authorization = Authorization
	return config
})
// 设置 Vue.prototype.$http 为 axios 的别名：
Vue.prototype.$http = service

