<template>
	<div class="hello">
		<h1>pixiv排行榜 &nbsp; {{apidata.mode}} : {{apidata.date}}</h1>

		<el-row>
			类型：<el-select v-model="imgtype.mode" placeholder="请选择">
				<el-option v-for="item in imgtypeoptions.mode" :key="item.value" :label="item.label"
					:value="item.value">
				</el-option>
			</el-select>
			<el-divider direction="vertical"></el-divider>
			<el-switch v-model="newdate" active-text="按日期" inactive-text="最新" @change="newdateChange"></el-switch>
			<el-date-picker :disabled="!newdate" v-model="imgtype.date" type="date" placeholder="选择日期"
				value-format="yyyyMMdd" :picker-options="pickerOptions0">
			</el-date-picker>
			<el-divider direction="vertical"></el-divider>
			<el-button type="primary" @click="getApi">查询</el-button>
		</el-row>

		<el-divider></el-divider>

		<el-row :gutter="20" class="pixivcards">
			<el-col :span="6" v-for="(ocol,icol) in imgdate">
				<el-badge :value="imgdate[icol].illust_page_count" class="item">
					<div class="grid-content bg-purple">
						<el-card :body-style="{ padding: '0px' }">
							<!-- <el-image :src="imgdate[icol].url" :preview-src-list="imgdate[icol].preview" lazy> -->
							<el-image :src="imgdate[icol].url" @click="imgLook(icol)" fit="cover" lazy></el-image>
							<div style="padding: 14px;height: 110px;">
								<span>标题:{{imgdate[icol].title}}</span><br>
								<span>作者:{{imgdate[icol].user_name}}</span><br>
								<span>pid:{{imgdate[icol].illust_id}}</span><br>
								<span>时间:{{imgdate[icol].date}}</span>
							</div>
						</el-card>
					</div>
				</el-badge>
			</el-col>
		</el-row>
		<div id="" align="center" v-if="imgtype.isget">
			<el-button type="primary" @click="addimg">更多</el-button>
		</div>

		<el-dialog :title="'标题:'+preview.data.title" :visible.sync="dialogTableVisible" class="pixivdialog">
			<p>ID:{{preview.data.illust_id}}</p>
			<p>作者:{{preview.data.user_name}}</p>
			<p>时间:{{preview.data.date}}</p>
			<el-tag v-for="(ocol,icol) in preview.data.tags" style="margin: 2px;">{{preview.data.tags[icol]}}</el-tag>
			<ul class="infinite-list" style="overflow:auto">
				<li v-for="(ocol,icol) in preview.arr1" class="infinite-list-item">
					<el-image :src="preview.arr1[icol]" :preview-src-list="[preview.arr2[icol]]" lazy>
					</el-image>
				</li>
			</ul>
		</el-dialog>

	</div>
</template>

<script>
	export default {
		name: 'HelloWorld',
		props: {
			msg: String
		},
		data() {
			return {
				newdate: false,
				pickerOptions0: {
					disabledDate(time) {
						return time.getTime() > Date.now() - 24 * 3600 * 1000 * 2;
					}
				},
				imgtype: {
					mode: 'daily',
					num: 10,
					date: '',
					page: 1,
					isget: false
				},
				imgtypeoptionsMap: new Map(),
				dialogTableVisible: false,
				imgtypeoptions: {
					mode: [{
						value: 'monthly',
						label: '月'
					}, {
						value: 'weekly',
						label: '周'
					}, {
						value: 'daily',
						label: '天'
					}, {
						value: 'weekly_r18',
						label: '周-R18'
					}, {
						value: 'daily_r18',
						label: '天-R18'
					}]
				},
				imgdate: [],
				originalurl: '',
				apidata: {
					date: "",
					mode: "",
					rank_total: 0,
				},
				preview: {
					arr1: [],
					arr2: [],
					data: {
						"date": "2021年01月01日 12:12",
						"user_name": "",
						"title": "",
						"illust_id": 0,
						"tags": [
							"0"
						],
						"illust_page_count": "0",
						"rating_count": 0,
						"profile_img": "",
						"yes_rank": 0,
						"user_id": 0,
						"rank": 0,
						"attr": "",
						"view_count": 0,
					},
				},
			}
		},
		beforeCreate() {
			document.title = 'xiaohu'
		},
		mounted() {
			this.imgtypeoptionsMap.set('monthly', '月')
			this.imgtypeoptionsMap.set('weekly', '周')
			this.imgtypeoptionsMap.set('daily', '天')
			this.imgtypeoptionsMap.set('weekly_r18', '周-R18')
			this.imgtypeoptionsMap.set('daily_r18', '天-R18')
			this.getApi();
		},
		methods: {
			imgLook(icol) {
				this.dialogTableVisible = true
				var other = this
				this.preview.arr1 = this.imgdate[icol].preview
				this.preview.arr2 = this.imgdate[icol].previewEx
				this.preview.data = this.imgdate[icol]
				if (this.imgdate[icol].preview.length == 0) {
					var databody = JSON.stringify({
						"url": "https://www.pixiv.net/ajax/illust/" + this.imgdate[icol].illust_id + "/pages",
						"method": "GET",
						"header": {
							"user-agent": "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/103.0.0.0 Safari/537.36",
							"cookie": "PHPSESSID=16147972_y54YP3wWfioDWIkUGVPIYMAcVmc5Zxt4"
						},
						"mediaType": null,
						"mediaTypeData": null
					});
					this.$http({
						method: "post",
						url: this.global.proxyapi,
						data: databody,
						headers: {
							'Content-Type': 'application/json;charset=UTF-8'
						}
					}).then(function(response) {
						for (var i = 0; i < response.data.body.length; i++) {
							other.imgdate[icol].preview.push(response.data.body[i].urls.small
								.replace("i.pximg.net", "i.pixiv.re"))
							other.imgdate[icol].previewEx.push(response.data.body[i].urls.original
								.replace("i.pximg.net", "i.pixiv.re"))
						}
					}).catch(function(error) {
						console.log(error);
					});
				}
			},
			newdateChange(e) {
				var nowDate = new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * 5);
				this.imgtype.date = nowDate.getFullYear() +
					((nowDate.getMonth() + 1) > 9 ? ('' + (nowDate.getMonth() + 1)) : ('0' + (nowDate.getMonth() + 1))) +
					(nowDate.getDate() > 9 ? ('' + nowDate.getDate()) : ('0' + nowDate.getDate()));
			},
			getApi: function() {
				this.imgtype.page = 1
				this.imgtype.isget = true
				var other = this;
				var databody = JSON.stringify({
					"url": "https://www.pixiv.net/ranking.php?mode=" + this.imgtype.mode +
						"&content=illust&p=" + this.imgtype.page + "&format=json&" + (!this.newdate ? "" : (
							"date=" + this.imgtype.date)),
					"method": "GET",
					"header": {
						"user-agent": "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/103.0.0.0 Safari/537.36",
						"cookie": "PHPSESSID=16147972_y54YP3wWfioDWIkUGVPIYMAcVmc5Zxt4"
					},
					"mediaType": null,
					"mediaTypeData": null
				});
				this.$http({
					method: "post",
					url: this.global.proxyapi,
					data: databody,
					headers: {
						'Content-Type': 'application/json;charset=UTF-8'
					}
				}).then(function(response) {
					if (response.data.contents) {
						other.imgdate = response.data.contents
						other.apidata.date = !other.newdate ? "最新" : other.imgtype.date
						other.apidata.mode = other.imgtypeoptionsMap.get(other.imgtype.mode)
						other.apidata.rank_total = response.data.rank_total
						for (var i = 0; i < other.imgdate.length; i++) {
							other.imgdate[i].preview = [];
							other.imgdate[i].previewEx = [];
							other.imgdate[i].url = other.imgdate[i].url.replace("i.pximg.net",
								"i.pixiv.re")
						}
						if (other.apidata.rank_total == other.imgdate.length) {
							other.imgtype.isget = false;
						}
					} else {
						other.$message.error(response.data.error);
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			lookimg: function(i) {
				this.originalurl = this.imgdate[i].meta_single_page.original_image_url
			},
			addimg: function() {
				if (this.apidata.rank_total == this.imgdate.length) {
					this.imgtype.isget = false;
					return;
				}
				this.imgtype.page++;
				var other = this;
				var databody = JSON.stringify({
					"url": "https://www.pixiv.net/ranking.php?mode=" + this.imgtype.mode +
						"&content=illust&p=" + this.imgtype.page + "&format=json&" + (!this.newdate ? "" : (
							"date=" + this.imgtype.date)),
					"method": "GET",
					"header": {
						"user-agent": "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/103.0.0.0 Safari/537.36",
						"cookie": "PHPSESSID=16147972_y54YP3wWfioDWIkUGVPIYMAcVmc5Zxt4"
					},
					"mediaType": null,
					"mediaTypeData": null
				});
				this.$http({
					method: "post",
					url: this.global.proxyapi,
					data: databody,
					headers: {
						'Content-Type': 'application/json;charset=UTF-8'
					}
				}).then(function(response) {
					if (response.data.contents) {
						// other.imgdate = response.data.contents
						for (var i = 0; i < response.data.contents.length; i++) {
							response.data.contents[i].preview = [];
							response.data.contents[i].previewEx = [];
							response.data.contents[i].url = response.data.contents[i].url.replace(
								"i.pximg.net",
								"i.pixiv.re")
							other.imgdate.push(response.data.contents[i])
						}
						if (other.apidata.rank_total == other.imgdate.length) {
							other.imgtype.isget = false;
						}
					} else {
						other.$message.error(response.data.error);
					}
				}).catch(function(error) {
					console.log(error);
				});

			}
		}
	}
</script>

<style>
	.el-card__body>.el-image {
		width: 300px;
		height: 300px;
	}

	.pixivcards>.el-col {
		padding-bottom: 20px;
		padding-top: 20px;
	}
	.pixivdialog > div >.el-dialog__body {
		padding-top: 0px !important;
	}
</style>
